import { Card, CardHeader } from "@/components/ui/card";
import {
  Select,
  SelectContent,
  SelectGroup,
  SelectItem,
  SelectLabel,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import React from "react";

interface Scanner {
  organization_name: string;
  address: string;
  profile_picture: string;
  id: number;
}

const ScannerList: Scanner[] = [
  {
    organization_name: "ABC",
    address: "Berlin, st:12",
    profile_picture: "https://github.com/shadcn.png",
    id: 1,
  },
  {
    organization_name: "Org",
    address: "Berlin, st:12",
    profile_picture: "https://github.com/shadcn.png",
    id: 2,
  },
  {
    organization_name: "Check",
    address: "Berlin, st:12",
    profile_picture: "https://github.com/shadcn.png",
    id: 11,
  },
  {
    organization_name: "Test",
    address: "Berlin, st:12",
    profile_picture: "https://github.com/shadcn.png",
    id: 165,
  },
  {
    organization_name: "Scanzer",
    address: "Berlin, st:12",
    profile_picture: "https://github.com/shadcn.png",
    id: 14,
  },
  {
    organization_name: "G-Scan",
    address: "Berlin, st:12",
    profile_picture: "https://github.com/shadcn.png",
    id: 13,
  },
];

const Scanner = () => {
  const [selectedOrganization, setSelectedOrganization] =
    React.useState<Scanner | null>(null);

  const handleValueChange = (value: string) => {
    const selected = ScannerList.find(
      (scanner) => scanner.id.toString() === value
    );
    setSelectedOrganization(selected || null);
  };

  return (
    <div className="space-y-4">
      <Select onValueChange={handleValueChange}>
        <SelectTrigger className="w-[280px]">
          {selectedOrganization ? (
            <div className="flex items-center gap-2">
              <Avatar className="h-6 w-6">
                <AvatarImage src={selectedOrganization.profile_picture} />
                <AvatarFallback>
                  {selectedOrganization.organization_name.charAt(0)}
                </AvatarFallback>
              </Avatar>
              <span>{selectedOrganization.organization_name}</span>
            </div>
          ) : (
            <SelectValue placeholder="Select an organization" />
          )}
        </SelectTrigger>
        <SelectContent>
          <SelectGroup>
            <SelectLabel className="px-2">Organizations</SelectLabel>
            <div className="max-h-60 overflow-y-auto space-y-2 p-2 w-full">
              {ScannerList.map((scanner) => (
                <SelectItem
                  key={scanner.id}
                  value={scanner.id.toString()}
                  className="w-full"
                >
                  <Card className="cursor-pointer hover:bg-gray-50 w-full">
                    <div className="w-full">
                      <Avatar className="h-8 w-8">
                        <AvatarImage src={scanner.profile_picture} />
                        <AvatarFallback>
                          {scanner.organization_name.charAt(0)}
                        </AvatarFallback>
                      </Avatar>
                      <div className="w-full">
                        <h3 className="text-sm font-medium">
                          {scanner.organization_name}
                        </h3>
                        <p className="text-xs text-gray-500">
                          {scanner.address}
                        </p>
                      </div>
                    </div>
                  </Card>
                </SelectItem>
              ))}
            </div>
          </SelectGroup>
        </SelectContent>
      </Select>

      {selectedOrganization && (
        <div className="p-4 border rounded-lg">
          <h3 className="font-medium">Selected Organization:</h3>
          <div className="mt-2 flex items-center gap-3">
            <Avatar className="h-10 w-10">
              <AvatarImage src={selectedOrganization.profile_picture} />
              <AvatarFallback>
                {selectedOrganization.organization_name.charAt(0)}
              </AvatarFallback>
            </Avatar>
            <div>
              <p className="font-medium">
                {selectedOrganization.organization_name}
              </p>
              <p className="text-sm text-gray-600">
                {selectedOrganization.address}
              </p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Scanner;
